/*******************************
     User Global Variables
*******************************/
/* Backgrounds */
.react-contextmenu-wrapper {
  width: 100%;
  height: 100%;
}
.resizeHandle {
  background-image: url("/img/handle.png");
  background-position: bottom right;
  background-repeat: no-repeat;
  box-sizing: border-box;
}
.icon-container {
  display: inline-block;
  padding: 2px 2px;
  border-radius: 4px;
  color: hsla(0, 0%, 100%, 0.7);
  transition: 0.5s;
}
.icon-container,
.icon-container * {
  cursor: pointer !important;
  cursor: hand !important;
}
.icon-container:hover {
  background: rgba(255, 255, 255, 0.07);
  box-shadow: inset 0 -1px 0 hsla(0, 0%, 100%, 0.07), inset 0 1px 2px rgba(0, 0, 0, 0.2);
}
.icon-container i.icon {
  font-size: 13px;
  line-height: 13px;
  margin: 0 auto;
}
.icon-container.notice {
  color: #cfc36e;
}
#container {
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  position: relative;
  box-sizing: border-box;
  background-color: #554121;
  box-shadow: inset 0 1px 1px 0 #cfc36e, inset 0 0 1px 0 hsla(53, 50%, 62%, 0.5), 0 0 1px 2px rgba(0, 0, 0, 0.5), 0 2px 2px 2px rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  padding: 5px;
  margin: 0 auto;
  margin-top: 4px;
}
#container #inner {
  width: 100%;
  height: 100%;
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  background-image: linear-gradient(#3a3a3a, #1e1e1e 2rem);
  box-shadow: inset 0 0 0 1px hsla(0, 0%, 100%, 0.1), 0 0 0 2px #222, 0 3px 1px #cfc36e, 0 0 2px 1px rgba(0, 0, 0, 0.4);
  border-radius: 4px;
  overflow: hidden;
}
#container #inner .split {
  width: 100%;
  border: none;
  height: 1px;
  min-height: 1px;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.4), inset 0 -1px 0 hsla(0, 0%, 100%, 0.1);
}
#container #inner #header {
  width: 100%;
  padding: 4px 8px;
  position: relative;
}
#container #inner #header .title {
  color: hsla(0, 0%, 100%, 0.7);
  font-size: 8px;
  line-height: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
  float: left;
}
#container #inner #header .subtitle {
  color: hsla(0, 0%, 100%, 0.5);
  padding-right: 16px;
  font-size: 8px;
  line-height: 8px;
  text-transform: uppercase;
  float: right;
  font-style: italic;
}
#container > .toggle {
  position: absolute;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #554121;
  box-shadow: inset 0 2px 2px 0 #cfc36e, inset 0 0 2px 0 hsla(53, 50%, 62%, 0.5), 0 0 2px 2px rgba(0, 0, 0, 0.5), 0 2px 2px 2px rgba(0, 0, 0, 0.5);
  cursor: pointer;
  cursor: hand;
  touch-action: manipulation;
  transition: 0.5s;
  opacity: 0;
  z-index: 2;
}
#container > .toggle.top {
  top: 1px;
}
#container > .toggle.bottom {
  bottom: 1px;
}
#container > .toggle.left {
  left: 1px;
}
#container > .toggle.right {
  right: 1px;
}
#container > .toggle:hover {
  opacity: 1;
}
#container #inner #game-state {
  background: rgba(0, 0, 0, 0.4);
  box-shadow: inset 0 -1px 0 hsla(0, 0%, 100%, 0.1);
  padding: 0 8px 2px 8px;
  color: #bca98a;
  font-size: 10px;
  text-transform: uppercase;
  font-weight: 600;
  position: relative;
}
#container #inner #game-state > span:first-child {
  display: inline-block;
  width: calc(100% - 65px - 5px);
  overflow-x: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  line-height: 10px;
  padding-top: 3px;
  height: 13px;
  overflow-y: hidden;
}
#container #inner #game-state > span:last-child {
  display: inline-block;
  float: right;
  width: 65px;
  text-align: right;
  white-space: nowrap;
  overflow-x: hidden;
}
#container #inner #game-state > span:last-child > span:first-child {
  vertical-align: top;
  padding-top: 3px;
  line-height: 10px;
  display: inline-block;
  margin-right: 3px;
}
#container #inner #game-state > span:last-child > .icon-container {
  padding: 2px 2px 2px 2px;
  line-height: 10px;
}
#container #inner #game-state > span:last-child > .icon-container > i.icon {
  font-size: 10px;
}
#container #inner #game-state .inactive:before {
  display: inline-block;
  content: 'Inactive: ';
  white-space: pre;
}
#container #inner #content {
  display: block;
  width: 100%;
  max-height: 100%;
  flex-basis: 0%;
  flex-grow: 1;
  flex-shrink: 1;
  overflow-y: scroll;
  scrollbar-width: none;
}
#container #inner #content #player-table {
  width: calc(100% - 16px);
  display: table;
  box-sizing: border-box;
  margin: 8px;
  border-collapse: collapse;
  position: relative;
}
#container #inner #content #player-table.grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex: 1;
}
#container #inner #content #player-table .row {
  background: rgba(255, 255, 255, 0.05);
  width: 100%;
  display: table-row;
  padding: 4px 0;
  color: hsla(0, 0%, 100%, 0.4);
  font-size: 11px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
#container #inner #content #player-table.grid .row {
  display: flex;
  flex-direction: column;
  flex: 1 1 calc(25% - 8px);
  width: calc(25% - 8px) !important;
  margin: 0 4px 4px 0;
  padding: 4px;
  box-sizing: border-box;
  background: rgba(0, 0, 0, 0.2);
}
#container #inner #content #player-table.grid .row:nth-child(4n),
#container #inner #content #player-table.grid .row:last-child {
  margin-right: 0;
}
#container #inner #content #player-table:not(.grid) .row:before {
  position: absolute;
  display: block;
  content: '';
  background: hsla(0, 0%, 100%, 0.05);
  height: 1px;
  width: 100%;
}
#container #inner #content #player-table .row .column {
  display: table-cell;
  min-width: fit-content;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  padding: 4px 3px;
  vertical-align: middle;
}
#container #inner #content #player-table .row.header {
  background: rgba(255, 255, 255, 0.03);
}
#container #inner #content #player-table .row.header .column {
  font-weight: bold;
  color: hsla(0, 0%, 100%, 0.7);
  text-transform: uppercase;
}
#container #inner #content #player-table .row.active {
  background: rgba(255, 255, 255, 0.15);
}
#container #inner #content #player-table .row.player,
#container #inner #content #player-table .row.player * {
  cursor: pointer;
}
#container #inner #content #player-table .row.player:not(.active):hover {
  background: rgba(255, 255, 255, 0.07);
}
#container #inner #content #player-table .row > .column:first-child,
#container #inner #content #player-table.grid .row .player-data > .column:first-child {
  width: 23px;
  padding: 3px 3px 0 3px;
}
#container #inner #content #player-table.grid .row .player-data > .column:first-child {
  width: 19px;
}
#container #inner #content #player-table.grid .row .player-data,
#container #inner #content #player-table.grid .row .stat-data {
  width: 100% !important;
  overflow: hidden;
}
#container #inner #content #player-table.grid .row .player-data .column:first-child {
  padding: 0;
  float: left;
}
#container #inner #content #player-table .row.player .column:first-child img {
  width: 20px;
  height: 20px;
}
#container #inner #content #player-table.grid .row .player-data .column:first-child img {
  width: 16px;
  height: 16px;
}
#container #inner #content #player-table .row > .column:nth-child(2),
#container #inner #content #player-table.grid .row .player-data > .column:nth-child(2) {
  padding-left: 0;
}
#container #inner #content #player-table .row > .column:nth-child(2) {
  margin-right: 3px;
  max-width: 100px;
}
#container #inner #content #player-table .row.player > .column:nth-child(2),
#container #inner #content #player-table.grid .row.player .player-data > .column:nth-child(2),
#container #inner #content #player-table.grid .row.player .stat-data > .column span {
  color: #bca98a;
}
#container #inner #content #player-table.grid .row.player .player-data > .column:nth-child(2) {
  margin-right: 0;
  float: left;
  width: calc(100% - 23px - 3px);
  min-width: 0;
  padding: 0;
  height: 16px;
  line-height: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#container #inner #content #player-table.grid .row.player .stat-data > .column {
  font-size: 9px;
  padding-left: 0;
  padding-top: 0;
  padding-bottom: 0;
}
#container #inner #content #player-table .row.player .blur {
  color: transparent !important;
  text-shadow: 0 0 5px rgba(255, 255, 255, 0.7);
}
#container #inner #content #player-detail {
  padding: 8px;
  display: flex;
  flex-direction: column;
}
#container #inner #content #player-detail .header {
  width: 100%;
  display: flex;
  flex-direction: row;
}
#container #inner #content #player-detail .header .job {
  max-width: 32px;
}
#container #inner #content #player-detail .header .job,
#container #inner #content #player-detail .header .name {
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: center;
}
#container #inner #content #player-detail .header .job {
  max-width: 32px;
}
#container #inner #content #player-detail .header .job img {
  width: 32px;
  height: 32px;
}
#container #inner #content #player-detail .header .name {
  padding-left: 8px;
  color: #bca98a;
  font-size: 28px;
}
#container #inner #content #player-detail .section {
  padding: 4px 0;
}
#container #inner #content #player-detail .split {
  margin-top: 8px;
}
#container #inner #content #player-detail .section > .title {
  color: rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
  font-weight: bold;
  font-size: 19px;
  padding: 6px 0 8px 0;
}
#container #inner #content #player-detail .section > .data {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
#container #inner #content #player-detail .section > .data .column {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin: 0 8px 8px 0;
  flex-basis: calc(50% - 16px);
  padding: 8px;
  box-sizing: border-box;
  background: rgba(0, 0, 0, 0.2);
}
#container #inner #content #player-detail .section > .data .column:nth-child(even),
#container #inner #content #player-detail .section > .data .column:last-child {
  margin-right: 0;
}
#container #inner #content #player-detail .section > .data .column > .title {
  color: rgba(255, 255, 255, 0.6);
  font-weight: bold;
  font-size: 12px;
}
#container #inner #content #player-detail .section > .data .column .value {
  color: rgba(255, 255, 255, 0.5);
}
#container #inner #content #player-detail .section:last-child {
  margin-bottom: -8px;
}
#container #inner #footer {
  width: 100%;
  padding: 0 8px 0 8px;
}
#container #inner #footer #role-links,
#container #inner #footer #navigation-links {
  float: left;
}
#container #inner #footer #footer-actions {
  float: right;
  text-align: right;
  box-sizing: border-box;
  padding: 0;
  line-height: 13px;
}
#container #inner #footer #role-links .role-link,
#container #inner #footer #navigation-links .navigation-link {
  padding: 4px 4px;
  display: inline-block;
  border-radius: 4px;
  color: hsla(0, 0%, 100%, 0.7);
  text-transform: uppercase;
  line-height: 9px;
  font-size: 9px;
  margin-right: 5px;
  transition: 0.5s;
  cursor: pointer;
  cursor: hand;
  touch-action: manipulation;
}
#container #inner #footer #role-links .role-link.active {
  background: rgba(255, 255, 255, 0.2);
  box-shadow: inset 0 -1px 0 hsla(0, 0%, 100%, 0.1), inset 0 1px 2px rgba(0, 0, 0, 0.4);
}
#container #inner #footer #role-links .role-link:not(.active):hover,
#container #inner #footer #navigation-links .navigation-link:not(.active):hover {
  background: rgba(255, 255, 255, 0.07);
  box-shadow: inset 0 -1px 0 hsla(0, 0%, 100%, 0.07), inset 0 1px 2px rgba(0, 0, 0, 0.2);
}
.placeholder {
  position: absolute;
  width: 2.5rem;
  height: 2.5rem;
  background: #554121;
  box-shadow: inset 0 1px 1px 0 #cfc36e, inset 0 0 1px 0 hsla(53, 50%, 62%, 0.5), 0 0 1px 2px rgba(0, 0, 0, 0.5), 0 2px 2px 2px rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  padding: 5px;
  touch-action: manipulation;
}
.placeholder,
.placeholder * {
  cursor: pointer;
  cursor: hand;
}
.placeholder.top {
  top: 3px;
}
.placeholder.bottom {
  bottom: 4px;
}
.placeholder.left {
  left: 3px;
}
.placeholder.right {
  right: 3px;
}
.placeholder .inner {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background: #1e1e1e;
  box-shadow: inset 0 0 0 1px hsla(0, 0%, 100%, 0.1), 0 0 0 2px #222, 0 3px 1px #cfc36e, 0 0 2px 1px rgba(0, 0, 0, 0.4);
  border-radius: 50%;
}
.placeholder .inner img {
  width: 2rem;
  height: 2rem;
  border: 2px solid transparent;
}
.container-context-menu {
  z-index: 50;
  border-top: 1px solid hsla(0, 0%, 100%, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  background: #282828;
  border-radius: 4px;
  text-shadow: 0 1px rgba(0, 0, 0, 0.6);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.6), 0 2px 3px rgba(0, 0, 0, 0.6);
  min-width: 10rem;
}
.container-context-menu .item-group {
  padding: 4px;
}
.container-context-menu .item-group .react-contextmenu-item {
  display: block;
  margin: 0.2rem 0;
  padding: 0.3rem 0.8rem;
  border-radius: 1rem;
  font-size: 0.9rem;
  line-height: 1;
  color: #eee1c5;
  cursor: pointer;
}
.container-context-menu .item-group .react-contextmenu-item:hover {
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.4), transparent);
}
.hidden {
  display: none !important;
}
.auto-height {
  height: auto !important;
}
.auto-height.down {
  position: absolute !important;
  bottom: 4px !important;
}
.react-tooltip {
  padding: 3px 6px;
  background: #554121 !important;
  font-size: 11px;
}
.react-tooltip:after {
  border-left-color: #554121 !important;
}
::-webkit-scrollbar {
  display: none;
}
